import React from 'react';
import './LoginPage.css';
import ThemeProvider from 'arui-feather/theme-provider';
import Heading from 'arui-feather/heading';
import Paragraph from 'arui-feather/paragraph';
import SimpleHeader from "../../components/SimpleHeader";
import {withRouter} from 'react-router-dom'
import BankContacts from "arui-private/bank-contacts";
import UsernameForm from "./UsernameForm";
import FixedBackground from "../../components/FixedBackground";

class LoginPage extends React.Component {
	
	componentWillMount() {
    if(localStorage.user) {
      let logout = window.confirm('Вы авторизованы. Выполнить выход из системы?');
      if (logout) {
      	console.log('yes');
      	return this.props.history.push('/logout');
      }
			else {
        console.log('cancel');
				return this.props.history.push('/');
      }
    }
	}

	render() {
		return (
			<div className="login-page">
				<FixedBackground/>
				<ThemeProvider theme="alfa-on-white">
					<div className="p-relative">
						<div className="container d-flex flex-column" style={{minHeight: '100vh'}}>
							<SimpleHeader/>
							<div className="login-form text-center py-3">
								<Heading size='l' className="mb-5">
									Альфа - Офис
								</Heading>
								<div className="form-wrapper mx-auto" style={{maxWidth: '100%', width: '300px'}}>
									<UsernameForm/>
								</div>
							</div>
							<div className="footer mt-auto mb-0">
								<BankContacts
									className="my-2 text-center"
									items={[ { phone: '2510  ', hint: 'звонки для юридических лиц' }]}
								/>
								<Paragraph className="text-center opacity-high">
									<span className="small">© 2001—2019 Альфа-Банк РК. v1.0.1</span>
								</Paragraph>
							</div>
						</div>
					</div>
				</ThemeProvider>
			</div>
		);
	}
}

export default withRouter(LoginPage);
