import React from 'react';
import Form from "arui-feather/form";
import FormField from "arui-feather/form-field";
import Input from "arui-feather/input";
import Spin from "arui-feather/spin";
import {CLEAR, ERROR, LOADING, SUCCESS} from "../../_types";
import {authService} from "../../_services/auth.service";
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import {pushNotification} from "../../_store/notices";
import {BAD_CREDENTIALS} from "../../_constants";
import GenericConfirmationModal from "arui-private/generic-confirmation-modal";
import {userService} from "../../_services/user.service";
import Button from "arui-feather/button";

class UsernameForm extends React.Component {
  
  constructor(props) {
    super(props);
    this.state = {
      phone: '',
      request: CLEAR,
      visible: false,
      isProcessing: false,
      confirmationError: '',
    };
    this.handleChange = this.handleChange.bind(this);
    this.handlePhoneFocus = this.handlePhoneFocus.bind(this);
    this.handlePhoneBlur = this.handlePhoneBlur.bind(this);
    this.handleFormSubmit = this.handleFormSubmit.bind(this);
    this.handleResendPassword = this.handleResendPassword.bind(this);
    this.handleInputFinished = this.handleInputFinished.bind(this);
  }
  
  get isFormValid() {
    const phoneRegex = /^\+[0-9] \([0-9]{3}\) [0-9]{3}-[0-9]{2}-[0-9]{2}$/gs;
    const err = [];
    if(!phoneRegex.test(this.state.phone)) {
      err.push({message: 'Неправильный формат номера', prop: 'phone'})
    }
    return err.length === 0;
  }
  get isLoading() {
    return this.state.request === LOADING
  }
  pushNotification(message, options) {
    this.props.pushNotification({message, options})
  }
  handleChange(prop, value) {
    this.setState({
      [prop]: value
    });
  }
  handlePhoneFocus() {
    if (this.state.phone !== "") return false;
    this.setState({ phone: "+77" });
  }
  handlePhoneBlur() {
    if (this.state.phone.length > 5) return false;
    this.setState({ phone: "" });
  }
  async handleFormSubmit() {
    if (!this.isFormValid) {
      this.pushNotification('Не верный формат номера', {status: 'error', title: 'Внимание!'});
      return
    }
    this.setState({request: LOADING});
    const username = this.state.phone.match(/\d+/g).map(Number).join('');
    
    authService.sendUsername(username).then(() => {
      this.setState({request: SUCCESS, visible: true})
    }).catch(e => {
      console.log({e});
      this.setState({request: ERROR});
      if (e.message ===  BAD_CREDENTIALS) this.pushNotification('Введены неверные данные', {status: 'error', title: 'Ошибка при входе', autoCloseDelay: 3000});
      else this.pushNotification('Попробуйте повторить запрос позже', {status: 'error', title: 'Ошибка сервера', autoCloseDelay: 3000});
    });
  }
  
  async handleResendPassword() {
    if (!this.isFormValid) {
      this.pushNotification('Не верный формат номера', {status: 'error', title: 'Внимание!'});
      return
    }
    const username = this.state.phone.match(/\d+/g).map(Number).join('');
    authService.resendPassword(username).then(() => {
    
    }).catch(e => {
      this.setState({request: ERROR});
      if (e.message ===  BAD_CREDENTIALS) this.pushNotification('Введены неверные данные', {status: 'error', title: 'Ошибка при входе', autoCloseDelay: 3000});
      else this.pushNotification('Попробуйте повторить запрос позже', {status: 'error', title: 'Ошибка сервера', autoCloseDelay: 3000});
    })
  }
  
  async handleInputFinished(password) {
    if (!this.isFormValid) {
      this.pushNotification('Не верный формат номера', {status: 'error', title: 'Внимание!'});
      return
    }
    const username = this.state.phone.match(/\d+/g).join('');
    this.setState({isProcessing: true});
    userService.login(username, password).then(() => {
      this.setState({confirmationError: ''});
      this.setState({isProcessing: false});
      this.props.history.push('/dashboard');
    }).catch(e => {
      console.log({e});
      if (e.message ===  BAD_CREDENTIALS) this.setState({confirmationError: 'Неверный код'});
      else this.pushNotification('Попробуйте повторить запрос позже', {status: 'error', title: 'Ошибка сервера', autoCloseDelay: 3000});
      this.setState({isProcessing: false});
    });
  }
  
  render() {
    return (
      <div>
        <Form onSubmit={this.handleFormSubmit} className="pb-0">
          <FormField className="pb-0">
            <Input
              width="available"
              type="tel"
              name="phone"
              className="text-left"
              mask={"+7 (711) 111-11-11"}
              placeholder="+7 (700) 000-00-00"
              label="Введите номер телефона"
              value={this.state.phone}
              clear={!this.isLoading}
              onFocus={this.handlePhoneFocus}
              onBlur={this.handlePhoneBlur}
              onChange={v => {
                this.handleChange("phone", v);
              }}
              rightAddons={ this.isLoading && <Spin size="m" visible={ true }/>}
            />
          </FormField>
          <Button type="submit" view="extra" className="my-4" disabled={this.isLoading || !this.isFormValid}>Войти</Button>
        </Form>
        <GenericConfirmationModal
          isVisible={ this.state.visible }
          isProcessing={ this.state.isProcessing }
          disabled={ this.state.isProcessing }
          onClose={ () => this.setState({ visible: false }) }
          error={ this.state.confirmationError }
          countdownDuration={ 60000 }
          onSmsRetryClick={ this.handleResendPassword }
          phone={ this.state.phone }
          onInputFinished={ this.handleInputFinished }
        />
      </div>
    )
  }
}


export default withRouter(connect(
  () => ({}),
  dispatch => ({
    pushNotification: (notification) => dispatch(pushNotification(notification))
  })
)(UsernameForm));
