import React from "react";
import bgImage from "../assets/images/bg-pattern-2.jpg";

export default function () {
	const background = `url(${bgImage})`;
	const backgroundGradient = `linear-gradient(180deg,rgba(255,255,255,0.9) 30%,rgba(255,255,255,0) 55%)`;
	
	return (
		[
			<div key={1} className="fixed-background" style={{background, backgroundSize: '120px'}}/>,
			<div key={2} className="fixed-background" style={{background: backgroundGradient}}/>
		]
	)
}
